import { graphql, useStaticQuery } from "gatsby";

export const useStaticAssets = () => {
  const data = useStaticQuery(graphql`
    query StaticAssetsQuery {
      team: allFile(filter: { sourceInstanceName: { eq: "team" }, relativeDirectory: { eq: "" } }) {
        nodes {
          relativePath
          childImageSharp {
            gatsbyImageData(
              layout: FIXED
              placeholder: BLURRED
              formats: [AUTO, WEBP, AVIF]
              width: 500
              height: 350
              transformOptions: { cropFocus: CENTER }
              quality: 80
            )
          }
        }
      }
      services: allFile(filter: { sourceInstanceName: { eq: "services" }, relativeDirectory: { eq: "" } }) {
        nodes {
          relativePath
          childImageSharp {
            gatsbyImageData(
              layout: CONSTRAINED
              placeholder: BLURRED
              formats: [AUTO, WEBP, AVIF]
              width: 500
              height: 500
              transformOptions: { cropFocus: CENTER }
              quality: 80
              breakpoints: [200, 300, 600, 900, 1200]
            )
          }
        }
      }
      images: allFile(filter: { sourceInstanceName: { eq: "images" }, relativeDirectory: { eq: "" } }) {
        nodes {
          relativePath
          childImageSharp {
            gatsbyImageData(
              layout: FIXED
              placeholder: BLURRED
              formats: [AUTO, WEBP, AVIF]
              width: 500
              height: 350
              transformOptions: { cropFocus: CENTER }
              quality: 80
            )
          }
        }
      }
      icons: allFile(filter: { sourceInstanceName: { eq: "icons" }, extension: { eq: "png" } }) {
        nodes {
          relativePath
          childImageSharp {
            gatsbyImageData(
              layout: FIXED
              placeholder: DOMINANT_COLOR
              formats: [AUTO, WEBP, AVIF]
              width: 64
              height: 64
            )
          }
        }
      }
      videos: allFile(filter: { sourceInstanceName: { eq: "videos" } }) {
        nodes {
          relativePath
          publicURL
        }
      }
    }
  `);

  const team = data.team.nodes.reduce((acc, node) => {
    acc[node.relativePath] = node.childImageSharp.gatsbyImageData;
    return acc;
  }, {});
  const services = data.services.nodes.reduce((acc, node) => {
    acc[node.relativePath] = node.childImageSharp.gatsbyImageData;
    return acc;
  }, {});
  const generics = data.images.nodes.reduce((acc, node) => {
    acc[node.relativePath] = node.childImageSharp.gatsbyImageData;
    return acc;
  }, {});

  const icons = data.icons.nodes.reduce((acc, node) => {
    acc[node.relativePath] = node.childImageSharp.gatsbyImageData;
    return acc;
  }, {});

  const videos = data.videos.nodes.reduce((acc, node) => {
    acc[node.relativePath] = node.publicURL;
    return acc;
  }, {});

  console.log(generics);

  const staticAssets = {
    images: {
      team,
      services,
      generics,
    },
    icons,
    videos,
  };

  return staticAssets;
};
